.about_me_container {
    width: 90%;
    margin: auto;
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 50px;
    padding: 50px 0;
}

.about_me_container .content_container {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.about_me_text {
    color: rgb(200, 200, 200);
    line-height: 25px;
}

.about_me_text a {
    color: var(--main_color_theme);
    transition: 0.3s;
    cursor: pointer;
    text-decoration: none;
}

.about_me_text a:hover {
    border-bottom: 1px solid var(--main_color_theme);

}


.pfp_container {
    height: 50vh;
    width: auto;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about_me_pfp {
    display: block;
    width: auto;
    height: 100%;
    border-radius: 5px;
}


.about_me_pfp_cover {
    border-radius:  5px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: var(--main_color_theme);
    opacity: 0.5;
    transition: 0.3s ease-in;
}

.about_me_pfp_cover:hover {
    opacity: 0;
    
}

@media screen and (max-width: 850px) {
    .about_me_container .content_container {
        width: 90%;
    }
    .about_me_container {
        flex-direction: column;
        padding: 20px 0;
        font-size: 18px;
        border-radius: 5px;
        background-color: #1d1d1dad;
    }

    .pfp_container {
        width: 70vw;
        height: auto;
    }

    .about_me_pfp {
        width: 90%;
        height: auto;
    }

    .about_me_pfp_cover {
        width: 90%;
        right: auto;
        left: auto;
    }

}